"use client"

import Button from '@mui/material/Button';

export default function Refer() {

    return (
    <>
      <div className="">
        <h2 className='text-(color:--my1) text-4xl font-bold'>Refer Candidate</h2>
        <p className='text-gray-500'>Refer Candidate <span className='text-gray-500'>&gt;</span></p>
        <div className="flex flex-col items-center">
          <div className="w-2xl">
            <div className="flex justify-end">
            <Button variant="contained" 
      disableElevation
      disableRipple
      slotprops={{
        root: {
          className: "bg-green-500 hover:bg-green-700 text-white font-bold py-2 px-4 rounded-lg shadow-md",
        },
      }}>Contained</Button>
            </div>
            <div className="flex justify-center"></div>
          </div>
        </div>
      </div>
    </>
      
      
    )
  }